const HASURA_GRAPHQL_ENGINE_HOSTNAME = 'kurrip.hasura.app';

const scheme = (proto) => {
  return window.location.protocol === 'https:' ? `${proto}s` : proto;
};

export const GRAPHQL_URL = `${scheme(
  'http'
)}://${HASURA_GRAPHQL_ENGINE_HOSTNAME}/v1/graphql`;

export const REALTIME_GRAPHQL_URL = `${scheme(
  'ws'
)}://${HASURA_GRAPHQL_ENGINE_HOSTNAME}/v1/graphql`;

export const authClientId = '69bhm63TZadm6EjvMHwpuajeLbZPUkJx';
export const authDomain = 'kurrip.au.auth0.com';
export const callbackUrl = window.location.origin;

export function getConfig() {
  // Configure the audience here. By default, it will take whatever is in the config
  // (specified by the `audience` key) unless it's the default value of "YOUR_API_IDENTIFIER" (which
  // is what you get sometimes by using the Auth0 sample download tool from the quickstart page, if you
  // don't have an API).
  // If this resolves to `null`, the API page changes to show some helpful info about what to do
  // with the audience.
  const audience =
    audience && audience !== 'YOUR_API_IDENTIFIER' ? audience : null;

  return {
    domain: authDomain,
    clientId: authClientId,
    ...(audience ? { audience } : null),
  };
}
